"use client";

import React, { useEffect, useRef } from "react";
import Image from "next/image";

const HeroSection = () => {
  const imageRef = useRef(null);

  useEffect(() => {
    const imageElement = imageRef.current;

    const handleScroll = () => {
      const scrollPosition = window.scrollY;
      const scrollThreshold = 100;

      if (scrollPosition > scrollThreshold) {
        imageElement.classList.add("scrolled");
      } else {
        imageElement.classList.remove("scrolled");
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <section className="pt-40 pb-20 px-4" style={{ backgroundColor: "#0d021f" }}>
      <div className="container mx-auto text-center">
        <h1 
          className="text-5xl md:text-8xl lg:text-[105px] pb-6"
          style={{
            background: "linear-gradient(90deg, #8a2be2, #d8b9ff)",
            WebkitBackgroundClip: "text",
            WebkitTextFillColor: "transparent",
          }}
        >
          Smart Finance, <br />Smarter Decisions
        </h1>
        <p className="text-xl text-[#c0a9d6] mb-8 max-w-2xl mx-auto">
          An intelligent finance platform that tracks, analyzes, and optimizes your spending with real-time AI-driven insights.
        </p>
        <div className="hero-image-wrapper mt-5 md:mt-0">
          <div ref={imageRef} className="hero-image">
            <Image
              src="/banner.jpeg"
              width={1280}
              height={720}
              alt="Dashboard Preview"
              className="rounded-lg shadow-2xl border border-[#5b4bc9] mx-auto"
              priority
            />
          </div>
        </div>
      </div>
    </section>
  );
};

export default HeroSection;
